* {
    margin: 0;
    padding: 0;
}

.home_container {
    width: 100%;
    color: white;
    border-radius: 10px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.home_container h1{
    margin: 40px 0px 30px 50px;
    font-size: 40px;
    animation: bounce; 
    animation-duration: 3s; 
}
.line{
    width: 50px;
    height: 1px;
    color: white;
    padding: 2px;
}


@media (min-width: 568px) and (max-width: 1000px){
    .home_container h1{
        font-size: 35px;
    }
}
@media (max-width: 1000px) {
    .home_container h1{
        font-size: 25px;
    }
}
